@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.clear-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  min-height: 100%;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  color: #444444;
}

.wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .wrapper {
    width: 1220px;
    padding: 0 40px;
  }
}
.wrapper--map {
  height: 362px;
  width: auto;
  margin: auto;
  padding: 0;
}
@media (min-width: 768px) {
  .wrapper--map {
    height: 400px;
  }
}
.wrapper--coop-data {
  width: auto;
  margin: auto;
  padding: 0;
}
@media (min-width: 1300px) {
  .wrapper--coop-data {
    width: 1220px;
    margin: 0 auto;
    padding: 0 40px;
  }
}
@media (min-width: 768px) and (max-width: 1299px) {
