    padding-left: 70px;
    padding-right: 65px;
  }
}

.button {
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  background-color: #68b738;
  display: block;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  border: none;
  padding: 10px 0;
  margin: 0;
}
.button:hover, .button:active, .button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--main {
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  padding: 11px 0;
}
@media (min-width: 768px) {
  .button--main {
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
    line-height: 20px;
    font-family: "Oswald", "Arial", sans-serif;
    width: 244px;
    padding: 17px 0;
    margin-top: 48px;
    margin-left: 3px;
  }
}
@media (min-width: 1300px) {
  .button--main {
    margin-top: 52px;
    transform: translateX(-3px);
  }
}
.button--silver {
  background-color: #f2f2f2;
  color: #444444;
}
.button--silver:hover, .button--silver:active, .button--silver:focus {
  background-color: #ebebeb;
}
.button--silver:active {
  color: rgba(68, 68, 68, 0.3);
}

img {
  max-width: 100%;
  height: auto;
}

.page-header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 65px;
  padding: 0;
}
@media (max-width: 767px) {
