  }
}

@media (max-width: 767px) {
  .page-header--border::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #d9d9d9;
    top: 66px;
    left: 0;
    content: "";
  }
}

.page-header__logo {
  display: flex;
  text-decoration: none;
  align-items: center;
}
.page-header__logo:hover {
  opacity: 0.8;
}
