@media (max-width: 767px) {
  .page-header__main-nav {
    background-color: #ffffff;
    position: absolute;
    padding-top: 2px;
    top: 64px;
    left: 0;
    z-index: 5;
  }
}
@media (min-width: 768px) {
  .page-header__main-nav {
    margin-left: auto;
    max-width: 75%;
  }
}
@media (min-width: 1300px) {
  .page-header__main-nav {
    max-width: 50%;
  }
}

@media (max-width: 767px) {
  .page-header--nav-closed .page-header__main-nav {
    display: none;
  }

  .page-header--nojs .page-header__main-nav {
    display: block;
    position: static;
    margin-top: 9px;
  }

  .page-header--nojs .page-header__button {
    display: none;
  }
}
.page-header__button {
  width: 38px;
  height: 38px;
  background-color: transparent;
  margin-left: auto;
  transform: translateX(7px);
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-header__button {
    display: none;
  }
}
.page-header__button:hover, .page-header__button:active, .page-header__button:focus {
  background-color: #ffffff;
}

.page-header--nav-closed .page-header__button::before {
  content: "";
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.page-header--nav-opened .page-header__button::before, .page-header--nav-opened .page-header__button::after {
  content: "";
  position: absolute;
  top: 48%;
  left: 18%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.page-header--nav-opened .page-header__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header--nav-opened .page-header__button::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-nav {
    width: 100%;
  }
}

