.living-proof {
  background-color: $grey-8;
  padding-top: 25px;
  padding-bottom: 44px;

  @include s768 {
    background-image: linear-gradient(to bottom, $white 57%, $grey-8 57%);
    padding-top: 40px;
    padding-bottom: 60px;
  }

  @include s1300 {
    background-image: linear-gradient(to bottom, $white 30%, $grey-8 30%);
    padding-top: 18px;
    padding-bottom: 33px;
  }
}

.living-proof__main {
  @include s1300 {
    width: 455px;
  }
}

.living-proof__wrapper {
  margin-top: 20px;

  @include s768 {
    margin-top: 54px;
  }

  @include s768-1299 {
    display: flex;
    align-items: center;
    margin-bottom: 85px;
  }

  @include s1300 {
    margin-top: 69px;
  }
}

.living-proof__changes {
  @include s1300 {
    width: 610px;
  }
}

.living-proof__title {
  @include o-r-s36-h40;
  color: $black;
  margin-top: 0;
  margin-bottom: 40px;

  @include s768 {
    @include o-r-s60-h60;
    margin-bottom: 63px;
  }

  @include s1300 {
    margin-top: 42px;
    margin-bottom: 147px;
  }
}

.living-proof__desc {
  font-family: "Arial", sans-serif;

  @include s768 {
    @include a-r-s16-h24;
    padding-right: 20px;
  }
}

.living-proof__expenses {
  margin-top: 34px;
  margin-bottom: 32px;

  @include s1300 {
    margin-top: 60px;
  }
}

.living-proof__flex {
  @include s1300 {
    display: flex;
    justify-content: space-between;
  }
}
